@import url('https://fonts.googleapis.com/css?family=Poppins');

* {
	font-family: 'Poppins'
}

.animation {
	height: 100vh;
	width: 100%;
	background-image: none;
	background-repeat: repeat;
	background-size: auto auto;
	background-repeat: no-repeat;
	background-size: 800% 400%;
	-webkit-animation-name: bg_animation;
	        animation-name: bg_animation;
	-webkit-animation-duration: 15s;
	        animation-duration: 15s;
	-webkit-animation-timing-function: linear;
	        animation-timing-function: linear;
	-webkit-animation-delay: 0;
	        animation-delay: 0;
	-webkit-animation-iteration-count: forwards;
	        animation-iteration-count: forwards;
	-webkit-animation-direction: alternate;
	        animation-direction: alternate;
	-webkit-animation-play-state: running;
	        animation-play-state: running;
}

.fadeIn{
	-webkit-animation-duration: 1.5s;
	        animation-duration: 1.5s;
}


@-webkit-keyframes bg_animation {
	0% {
		background-color: #4facfe;
	}

	20% {
		background-color: #4088f2;
	}

	40% {
		background-color: #6146b3;
	}

	60% {
		background-color: #753a88;
	}

	80% {
		background-color: #cc2b5e;
	}


	100% {
		background-color: #f77889;
	}
}


@keyframes bg_animation {
	0% {
		background-color: #4facfe;
	}

	20% {
		background-color: #4088f2;
	}

	40% {
		background-color: #6146b3;
	}

	60% {
		background-color: #753a88;
	}

	80% {
		background-color: #cc2b5e;
	}


	100% {
		background-color: #f77889;
	}
}


