@import url('https://fonts.googleapis.com/css?family=Poppins');

h3, p {
  font-weight: 100;
}

.main-container {
  height: 100vh;
  font-family: 'Poppins';
  background: -webkit-gradient(linear, left top, right top, from(#4088f2), to(#6146b3));
  background: -webkit-linear-gradient(left, #4088f2 0%, #6146b3 100%);
  background: -o-linear-gradient(left, #4088f2 0%, #6146b3 100%);
  background: linear-gradient(to right, #4088f2 0%, #6146b3 100%);
}

#bg-img {
  height: 100vh;
  width: 100%;
  opacity: 0.06;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 0;
  position: absolute;
}

.main-content {
  width: 70%;
  height: 75vh;
  margin: 0px 2.5%;
}

.fadeIn{
  -webkit-animation-duration: 3.5s;
          animation-duration: 3.5s;
}

.flex-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.sidebar {
  width: 22.5%;
  height: 75vh;
  margin-right: 2.5%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}



